import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import api from "@/microApps/deviceManage/api";
import StatusConditionConfig from "@/microApps/deviceManage/views/deviceManage/components/StatusConditionConfig.vue";
let uuid = 1;

@Component({
  components: { StatusConditionConfig }
})
export default class DeviceAlarmCondition extends Vue {
  @Prop({ type: String, required: true })
  deviceId!: string;
  @Prop({ type: Array, required: true })
  allStatusList!: any[];
  @Prop({ type: Array, required: true })
  sensorList!: any[];
  conditions: any[] = [];
  disableStatusIdMap: any = {};
  roleList: any[] = [];
  sensorIdAttrMap: any = {};

  created() {
    this.getConditionList();
    this.getRoleList();
  }

  handleAddCondition() {
    this.conditions.unshift({
      key: uuid++,
      pushMsg: false
    });
  }

  async getConditionList() {
    let {
      data: { data, status, message }
    } = await this.$http.get(api.listWarningTerms + this.deviceId);
    if (status) {
      data.forEach((item: any) => {
        item.statusDefinitionId = item.statusId;
      });
      this.conditions = data;
      let map: any = {};
      data.forEach((d: any) => {
        map[d.statusDefinitionId] = true;
      });
      this.disableStatusIdMap = map;
    } else {
      this.$message.error(message);
    }
  }

  // 
  async getSensorAttr(sensorId: string) {
    let {
      data: { data, status, message }
    } = await this.$http.get(api.getSensorAttr + sensorId);
    if (status) {
      let map = { ...this.sensorIdAttrMap };
      map[sensorId] = data;
      this.sensorIdAttrMap = map;
    } else {
      this.$message.error(message);
    }
  }

  // 
  async getRoleList() {
    let {
      data: { data, status, message }
    } = await this.$http.get(api.getRoleList);
    if (status) {
      this.roleList = data;
    } else {
      this.$message.error(message);
    }
  }
}
