 * @Author: liuyang
 * @Date: 2019-08-08 20:22
 */
import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import api from "@/microApps/deviceManage/api";
import { ElTable } from "element-ui/types/table";

@Component({})
export default class DeviceStyle extends Vue {
  @Prop({ type: String })
  deviceId!: string;
  tableData: Array<any> = [];
  tableLoading: boolean = false;
  statusDialog: boolean = false;
  statusDialogTitle: string = "";
  form: any = {};
  formLoading: any = false;
  formRules: any = {
    name: { required: true, message: "", trigger: "change" },
    code: { required: true, message: "", trigger: "change" }
  };

  conditionDialog: boolean = false;
  conditionLoading: boolean = false;
  conditionList: Array<any> = [];
  conditionIds: Array<any> = [];

  styleDialog: boolean = false;
  styleLoading: boolean = false;
  styleForm: any = {};

  created(): void {
    this.getTableData();
  }

  async getTableData() {
    this.tableLoading = true;
    try {
      let {
        data: { data, status, message }
      } = await this.$http.get(api.getDeviceStatusList + this.deviceId);
      if (status) {
        this.tableData = data;
      } else {
        this.$message.error(message);
      }
    } finally {
      this.tableLoading = false;
    }
  }

  handleAddStatus() {
    this.statusDialogTitle = "";
    this.statusDialog = true;
    this.form = {};
    this.$nextTick(() => {
      let form: any = this.$refs.form;
      form.clearValidate();
    });
  }

  handleEditStatus(item: any) {
    this.statusDialogTitle = "";
    this.statusDialog = true;
    this.form = JSON.parse(JSON.stringify(item));
    this.$nextTick(() => {
      let form: any = this.$refs.form;
      form.clearValidate();
    });
  }

  handleDeleteStatus(item: any) {
    this.$confirm(`?`, "", {
      confirmButtonText: "",
      cancelButtonText: "",
      type: "warning"
    }).then(() => {
      this.deleteStatus(item);
    });
  }

  handleSureStatus() {
    let form: any = this.$refs.form;
    form.validate((flag: boolean) => {
      if (flag) {
        this.formLoading = true;
        this.saveStatus();
      }
    });
  }

  // 
  async saveStatus() {
    try {
      let {
        data: { data, status, message }
      } = await this.$http.post(api[this.form.id ? "updateDeviceStatus" : "addDeviceStatus"], {
        ...this.form,
        deviceId: this.deviceId
      });
      this.formLoading = false;
      if (status) {
        this.$message.success(this.form.id ? "" : "");
        this.getTableData();
        this.statusDialog = false;
      } else {
        this.$message.error(message);
      }
    } finally {
      this.formLoading = false;
    }
  }

  // 
  async deleteStatus(item: any) {
    let {
      data: { data, status, message }
    } = await this.$http.get(api.deleteDeviceStatus + item.id);
    if (status) {
      this.conditionList = data;
      this.$message.success("");
      this.getTableData();
    } else {
      this.$message.error(message);
    }
  }

  // 
  async getConditionList() {
    let {
      data: { data, status, message }
    } = await this.$http.get(api.getConditionList + this.deviceId);
    if (status) {
      this.conditionList = data;
      this.$nextTick(() => {
        this.conditionList.forEach((item: any) => {
          (this.$refs.conditionTable as ElTable).toggleRowSelection(item, this.form.deviceConditions.some((d: any) => d.id === item.id));
        });
      });
    } else {
      this.$message.error(message);
    }
  }

  handleBindCondition(item: any) {
    this.getConditionList();
    this.conditionDialog = true;
    this.form = item;
  }

  handleSelectTable(selection: Array<any>) {
    this.conditionIds = selection.map((d: any) => d.id);
  }

  // 
  async addDeviceStatusConditions() {
    this.conditionLoading = true;
    try {
      let {
        data: { data, status, message }
      } = await this.$http.post(api.addDeviceStatusConditions + this.form.id, this.conditionIds);
      if (status) {
        this.$message.success("");
        this.conditionDialog = false;
        this.getTableData();
      } else {
        this.$message.error(message);
      }
    } finally {
      this.conditionLoading = false;
    }
  }

  async handleEditStyle(item: any) {
    this.styleDialog = true;
    this.form = item;
    this.styleForm = {
      styleColor: item.styleColor,
      styleSize: item.styleSize,
      styleTips: item.styleTips
    };
  }

  // 
  async handleSureStyle() {
    this.styleLoading = true;
    try {
      let {
        data: { data, status, message }
      } = await this.$http.post(api.updateDeviceStatusStyle + this.form.id, this.styleForm);
      if (status) {
        this.$message.success("");
        this.styleDialog = false;
        this.getTableData();
      } else {
        this.$message.error(message);
      }
    } finally {
      this.styleLoading = false;
    }
  }
}
