import { Vue, Component, Prop } from "vue-property-decorator";
import api from "@/microApps/deviceManage/api";
import { ElForm } from "element-ui/types/form";

@Component({})
export default class EventConditionConfig extends Vue {
  @Prop({ type: Number, required: true })
  index!: number;
  @Prop({ type: Array, required: true })
  sensorList!: any[];
  @Prop({ type: Array, required: true })
  allEventList!: any[];
  @Prop({ type: Object, required: true })
  disableEventIdMap!: any;
  @Prop({ type: Array, required: true })
  allStatusList!: any[];
  @Prop({ type: Object, required: true })
  data!: any;
  @Prop({ type: String, required: true })
  deviceId!: string;

  static focusInstance: null | EventConditionConfig = null; // 

  // 
  keyboardFocus: boolean = false;
  widgetFocus: boolean = false;
  cursorIndex: number | null = null;
  // 
  symbolList: any[] = [
    // { type: "symbol", code: "add", value: "+" },
    // { type: "symbol", code: "subtract", value: "-" },
    // { type: "symbol", code: "multiply", value: "", realValue: "*" },
    // { type: "symbol", code: "divide", value: "", realValue: "/" },
    // { type: "symbol", code: "rt", value: ">" },
    // { type: "symbol", code: "rtEqual", value: "", realValue: ">=" },
    // { type: "symbol", code: "lt", value: "<" },
    // { type: "symbol", code: "ltEqual", value: "", realValue: "<=" },
    { type: "lp", code: "lp", value: "(" },
    { type: "rp", code: "rp", value: ")" },
    { type: "symbol", code: "and", value: "and" },
    { type: "symbol", code: "or", value: "or" }
    // { type: "symbol", code: "equal", value: "==" },
    // { type: "symbol", code: "notEqual", value: "!=" },
    // { type: "symbol", code: "notEqual", value: "" },
    // { type: "symbol", code: "notEqual", value: "" }
  ];
  form: any = {};
  formRules: any = {
    remark: { required: true, message: "" },
    eventDefinitionId: { required: true, message: "" }
  };
  selectConditions: any[] = [];
  status: string = "start";
  lpNum: number = 0;
  rpNum: number = 0;

  // 
  customFuncTypes: any[] = [
    {
      name: "",
      type: "customFunc",
      funcName: "",
      widget: [{ type: "select", text: "" }, { text: "", isText: true }, { type: "input", text: "", scale: 60 }]
    },
    {
      name: "",
      type: "customFunc",
      funcName: "",
      widget: [
        { type: "select", text: "" },
        { type: "input", text: "", scale: 60 },
        { text: "", isText: true },
        { type: "input", text: "" }
      ]
    },
    {
      name: "",
      type: "customFunc",
      funcName: "",
      widget: [
        { type: "select", text: "" },
        { type: "input", text: "", scale: 60 },
        { text: "", isText: true },
        { type: "input", text: "" }
      ]
    }
  ];

  loading: boolean = false;

  // 
  get showExpression() {
    let map: any = {};
    this.allStatusList.forEach((item: any) => {
      map[item.id] = item;
    });
    return this.selectConditions
      .map((item: any) => {
        if (item.type === "customFunc") {
          return `${item.widget
            .map((d: any) => {
              if (d.isText) {
                return d.text;
              } else {
                if (d.type === "select") {
                  let status = map[d.value];
                  return status ? status.name : "";
                } else {
                  return d.value ? d.value + d.text : "";
                }
              }
            })
            .join("")}`;
        }
        return item.value;
      })
      .join(" ");
  }

  // 
  get realExpression() {
    return this.selectConditions
      .map((item: any) => {
        if (item.type === "customFunc") {
          return `${item.funcName}(${item.widget
            .filter((d: any) => !d.isText)
            .map((d: any) => {
              if (d.type === "select") {
                return "'" + this.deviceId + "_" + d.value + "'";
              } else {
                // 
                return d.scale ? d.value * d.scale : d.value;
              }
            })
            .join(",")})`;
        }
        return item.realValue || item.value;
      })
      .join(" ");
  }

  //  
  get accessEventList() {
    return this.allEventList.filter((d: any) => !this.disableEventIdMap[d.id] || d.id === this.data.eventDefinitionId);
  }

  created() {
    this.initData();
    document.addEventListener("click", this.handleBlurRuleInput, true);
    document.addEventListener("keydown", this.handleKeydown);
  }

  destroyed() {
    document.removeEventListener("click", this.handleBlurRuleInput);
    document.removeEventListener("keydown", this.handleKeydown);
    if (EventConditionConfig.focusInstance && EventConditionConfig.focusInstance === this) {
      EventConditionConfig.focusInstance = null;
    }
  }

  initData() {
    this.form = JSON.parse(JSON.stringify(this.data));
    let selectConditions = this.form.expressionJson;
    selectConditions = selectConditions ? JSON.parse(selectConditions) : [];
    this.selectConditions = selectConditions;
    // 
    let len = selectConditions.length;
    if (len) {
      this.status = selectConditions[len - 1].type;
      this.lpNum = selectConditions.filter((d: any) => d.type === "lp").length;
      this.rpNum = selectConditions.filter((d: any) => d.type === "rp").length;
    }
  }

  // 
  handleFocusRuleInput(index: number | null) {
    this.keyboardFocus = true;
    this.cursorIndex = index;
    if (EventConditionConfig.focusInstance && EventConditionConfig.focusInstance !== this) {
      EventConditionConfig.focusInstance.handleBlurRuleInput();
    }
    EventConditionConfig.focusInstance = this;
  }
  handleBlurRuleInput() {
    this.keyboardFocus = false;
    EventConditionConfig.focusInstance = null;
  }
  handleFocusWidgetInput() {
    this.widgetFocus = true;
  }
  handleBlurWidgetInput() {
    this.widgetFocus = false;
  }
  get reactKeydownEvent() {
    return this.keyboardFocus && !this.widgetFocus;
  }
  handleKeydown(e: KeyboardEvent) {
    if (!this.reactKeydownEvent) return;
    switch (e.code.toLocaleLowerCase()) {
      case "backspace":
        this.handleDeleteToken();
        break;
      case "arrowleft":
        this.handleMoveCursor(false);
        break;
      case "arrowright":
        this.handleMoveCursor(true);
        break;
    }
  }
  // 
  // start: 
  // symbol: 
  // lp: 
  // rp: 
  // input: 
  // status: 
  // customFunc: 
  handleSelectKey(item: any) {
    // switch (this.status) {
    //   case "start":
    //     if (item.type === "symbol") return;
    //     break;
    //   case "symbol":
    //     if (item.type === "symbol") return;
    //     break;
    //   case "lp":
    //     if (item.type === "symbol") return;
    //     break;
    //   case "rp":
    //     if (item.type !== "symbol" && item.type !== "rp") return;
    //     break;
    //   case "input":
    //     if (item.type !== "symbol" && item.type !== "rp") return;
    //     break;
    //   case "status":
    //     if (item.type !== "symbol" && item.type !== "rp") return;
    //     break;
    //   case "customFunc":
    //     if (item.type !== "symbol" && item.type !== "rp") return;
    //     break;
    // }
    // switch (item.code) {
    //   case "lp":
    //     this.lpNum++;
    //     break;
    //   case "rp":
    //     if (this.rpNum === this.lpNum) return;
    //     this.rpNum++;
    //     break;
    // }
    this.status = item.type;
    let data = JSON.parse(JSON.stringify(item));
    if (this.cursorIndex === null) {
      this.selectConditions.push(data);
    } else {
      this.selectConditions.splice(this.cursorIndex, 0, data);
      this.cursorIndex++;
    }
  }

  // 
  handleDeleteToken() {
    let item: any;
    if (this.cursorIndex === null) {
      item = this.selectConditions.pop();
    } else if (this.cursorIndex > 0) {
      item = this.selectConditions.splice(this.cursorIndex - 1, 1);
    }
    if (!item) return;
    this.cursorIndex !== null && this.cursorIndex--;
    switch (item.code) {
      case "lp":
        this.lpNum--;
        break;
      case "rp":
        this.rpNum--;
        break;
    }
    let last = this.selectConditions[this.selectConditions.length - 1];
    this.status = last ? last.type : "start";
  }

  // 
  handleMoveCursor(arrow: boolean) {
    if (arrow) {
      if (this.cursorIndex !== null && this.cursorIndex < this.selectConditions.length - 1) {
        this.cursorIndex++;
      } else if (this.cursorIndex === this.selectConditions.length - 1) {
        this.cursorIndex = null;
      }
    } else {
      if (this.cursorIndex === null) {
        this.cursorIndex = this.selectConditions.length - 1;
      } else if (this.cursorIndex > 0) {
        this.cursorIndex--;
      }
    }
  }

  // 
  checkWidget() {
    if (
      this.selectConditions.some((item: any) => {
        return item.type === "customFunc" ? item.widget.filter((d: any) => !d.isText).some((d: any) => !d.value) : false;
      })
    ) {
      this.$message.info("");
      return false;
    }
    return true;
  }

  // 
  async checkExpression() {
    if (!this.checkWidget()) return;
    let {
      data: { status, message }
    } = await this.$http.post(api.checkExpression, {
      expression: this.realExpression
    });
    if (status) {
      this.$message.success("");
    } else {
      this.$message.error(message);
    }
  }

  // 
  handleSaveCondition() {
    (this.$refs.form as ElForm).validate(async flag => {
      if (flag) {
        if (!this.checkWidget()) return;
        this.doSaveCondition();
      }
    });
  }

  async doSaveCondition() {
    this.loading = true;
    try {
      let {
        data: { status, message }
      } = await this.$http.post(this.form.id ? api.updateEventRule : api.addEventRule, {
        deviceId: this.deviceId,
        eventDefinitionId: this.form.eventDefinitionId,
        expression: this.realExpression,
        expressionShow: this.showExpression,
        expressionJson: JSON.stringify(this.selectConditions),
        id: this.form.id,
        remark: this.form.remark
      });
      if (status) {
        this.$message.success("");
        this.$emit("update");
      } else {
        this.$message.error(message);
      }
    } finally {
      this.loading = false;
    }
  }

  // 
  handleDeleteCondition() {
    if (this.form.id) {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.deleteCondition();
      });
    } else {
      this.$emit("delete");
    }
  }

  // 
  async deleteCondition() {
    let {
      data: { status, message }
    } = await this.$http.get(api.deleteByEventTermId + this.form.id);
    if (status) {
      this.$message.success("");
      this.$emit("update");
    } else {
      this.$message.error(message);
    }
  }
}
