














































import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import api from "@/microApps/sensorManage/api";
import utils from "@/utils/utils";

@Component({})
export default class SensorDetail extends Vue {
  @Prop({ type: String, default: "" })
  title!: string;
  @Prop({ type: Boolean, default: false })
  sensorDialogVisible!: boolean;
  @Prop({ type: Object })
  data?: object;
  @Prop({ type: String })
  deviceId?: string;

  providerOptions: any[] = [];

  visible: boolean = false;
  loading: boolean = false;
  addForm: any = {};
  typeOptions: Array<any> = [];
  typeOptionsProps: any = {
    value: "id",
    label: "name",
    showAllLevels: false,
    checkStrictly: true
  };
  addRules = {
    name: { required: true, trigger: "blur", message: "" },
    code: { required: true, trigger: "blur", message: "" },
    sensorTypeId: { required: true, trigger: "blur", message: "" },
    providerSensorId: {
      required: true,
      trigger: "blur",
      message: "ID"
    },
    providerCode: {
      required: true,
      trigger: "change",
      message: ""
    }
  };

  created() {
    this.getSensorTypeData();
    this.getSensorProviderList();
  }

  @Watch("sensorDialogVisible")
  visibleChange(dialogVisible: boolean) {
    this.visible = dialogVisible;
    if (dialogVisible) {
      this.$nextTick(() => {
        let ref: any = this.$refs.addForm;
        ref.resetFields();
        this.addForm = JSON.parse(JSON.stringify(this.data));
      });
    }
  }

  closeDialog() {
    this.visible = false;
    this.$emit("close");
  }

  handleAddSubmit() {
    let rest: any = this.$refs.addForm;
    rest.validate((valid: any) => {
      if (valid) {
        this.loading = true;
        if (typeof this.addForm.sensorTypeId !== "string") {
          this.addForm.sensorTypeId = utils.lastOfArr(this.addForm.sensorTypeId);
        }
        let url = this.addForm.id ? api.updateSensor : this.deviceId ? api.addSensorAndBind + this.deviceId : api.addSensor;
        this.$http
          .post(url, this.addForm)
          .then((res: any) => {
            this.loading = false;
            if (res.data.status) {
              this.$message.success(this.addForm.id ? "" : "");
              this.closeDialog();
              this.$emit("submit");
            } else {
              this.$message.error(res.data.message);
            }
          })
          .catch(() => {
            this.loading = false;
          });
      } else {
        return false;
      }
    });
  }

  //options
  getSensorTypeData() {
    this.$http.get(api.sensorTypeTree).then((res: any) => {
      if (res.data.status) {
        this.typeOptions = res.data.data;
        utils.removeEmptyChildren(this.typeOptions);
      } else {
        this.$message.error(res.data.message);
      }
    });
  }

  //
  getSensorProviderList() {
    this.$http.post(api.providerList, {}).then((res: any) => {
      if (res.data.status) {
        this.providerOptions = res.data.data;
      } else {
        this.$message.error(res.data.message);
      }
    });
  }
}
