 * @Describe: Describe
 * @Author: liuyang
 * @Date: 2019-08-02 10:58
 */
import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import api from "@/microApps/deviceManage/api";

@Component({})
export default class DeviceCondition extends Vue {
  @Prop({ type: String })
  deviceId!: string;
  conditionList: Array<any> = [];
  tableLoading: boolean = false;
  conditionDialog: boolean = false;
  conditionTitle: string = "";
  conditionDialogLoading: boolean = false;
  eventLoading: boolean = false;
  form: any = {};
  formRules: any = {
    name: { required: true, message: "", trigger: "change" },
    code: { required: true, message: "", trigger: "change" },
    eventDefinitionId: { required: true, message: "", trigger: "change" }
  };
  eventData: Array<any> = [];
  eventName: string = "";
  pagination: any = {
    current: 1,
    size: 99999
  };
  eventTotal: number = 0;

  configDialog: boolean = false;
  configList: Array<any> = [];

  sensorList: Array<any> = [];
  compareTypeList: Array<any> = [
    { label: "", value: "LE" },
    { label: "", value: "LT" },
    { label: "", value: "GE" },
    { label: "", value: "GT" },
    { label: "", value: "EQ" },
    { label: "", value: "NE" }
    // { label: "", value: "NOTIN" },
    // { label: "", value: "LE" },
    // { label: "", value: "ISNULL" },
    // { label: "", value: "NOTNULL" }
  ];
  typeOptions: Array<any> = [];

  created() {
    this.getConditionList();
  }

  handleAdd() {
    this.conditionTitle = "";
    this.form = {};
    this.conditionDialog = true;
    this.getEventData();
    this.$nextTick(() => {
      let form: any = this.$refs.form;
      form.clearValidate();
    });
  }

  // 
  async getEventData() {
    this.eventLoading = true;
    try {
      let {
        data: { data, status, message }
      } = await this.$http.post(api.getEventDefinition, {
        ...this.pagination,
        query: {
          name: ""
        }
      });
      if (status) {
        this.eventData = data.records;
        this.eventTotal = data.total;
      } else {
        this.$message.error(message);
      }
    } finally {
      this.eventLoading = false;
    }
  }

  // 
  handleSureAdd() {
    let form: any = this.$refs.form;
    form.validate((flag: boolean) => {
      if (flag) {
        this.saveCondition();
      }
    });
  }

  // 
  handleEdit(item: any) {
    this.conditionTitle = "";
    this.form = JSON.parse(JSON.stringify(item));
    this.conditionDialog = true;
    this.getEventData();
  }

  // 
  async saveCondition() {
    this.conditionDialogLoading = true;
    try {
      let {
        data: { data, status, message }
      } = await this.$http.post(this.form.id ? api.updateCondition : api.addCondition, {
        ...this.form,
        deviceId: this.deviceId
      });
      if (status) {
        this.getConditionList();
        this.conditionDialog = false;
        this.$message.success(this.form.id ? "" : "");
      } else {
        this.$message.error(message);
      }
    } finally {
      this.conditionDialogLoading = false;
    }
  }

  // 
  handleDelete(item: any) {
    this.$confirm(`?`, "", {
      confirmButtonText: "",
      cancelButtonText: "",
      type: "warning"
    }).then(() => {
      this.deleteCondition(item);
    });
  }

  async deleteCondition(item: any) {
    let {
      data: { data, status, message }
    } = await this.$http.get(api.deleteCondition + item.id);
    if (status) {
      this.getConditionList();
      this.$message.success("");
    } else {
      this.$message.error(message);
    }
  }

  // 
  async getConditionList() {
    this.tableLoading = true;
    try {
      let {
        data: { data, status, message }
      } = await this.$http.get(api.getConditionList + this.deviceId);
      if (status) {
        this.conditionList = data;
      } else {
        this.$message.error(message);
      }
    } finally {
      this.tableLoading = false;
    }
  }

  // 
  handleConfig(item: any) {
    this.getSensorList();
    this.form = item;
    this.configList = JSON.parse(JSON.stringify(item.items));
    this.configList.forEach((item: any, index: number) => {
      this.getSensorAttr(item, index);
    });
    if (!this.configList.length) {
      this.configList = [{}];
    }
    this.configDialog = true;
  }

  // 
  async getSensorList() {
    let {
      data: { data, status, message }
    } = await this.$http.get(api.getBindSensor + this.deviceId);
    if (status) {
      this.sensorList = data || [];
    } else {
      this.$message.error(message);
    }
  }

  // 
  async getSensorAttr(item: any, index: number) {
    let {
      data: { data, status, message }
    } = await this.$http.get(api.getSensorAttr + this.configList[index].sensorId);
    if (status) {
      this.configList[index].attrList = data;
      this.$forceUpdate();
    } else {
      this.$message.error(message);
    }
  }

  // 
  handleAddOne() {
    this.configList.push({
      attrList: []
    });
  }

  // 
  handleDeleteOne(index: number) {
    this.configList.splice(index, 1);
  }

  async handleSureConfig() {
    let params = this.configList.map((item: any) => {
      return {
        sensorId: item.sensorId,
        sensorAttributeId: item.sensorAttributeId,
        compareType: item.compareType,
        value: item.value
      };
    });
    let {
      data: { data, status, message }
    } = await this.$http.post(api.updateConditionItem + this.form.id, params);
    if (status) {
      this.configDialog = false;
      this.$message.success("");
      this.getConditionList();
    } else {
      this.$message.error(message);
    }
  }
}
