 * @Author: liuyang
 * @Date: 2019-11-22 16:06
 */
import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import api from "@/microApps/deviceManage/api";
import { ElTable } from "element-ui/types/table";
import { ElForm } from "element-ui/types/form";

@Component({})
export default class StatusConditionConfig extends Vue {
  @Prop({ type: Number, required: true })
  index!: number;
  @Prop({ type: Array, required: true })
  sensorList!: any[];
  @Prop({ type: Array, required: true })
  allStatusList!: any[];
  @Prop({ type: Object, required: true })
  disableStatusIdMap!: any;
  @Prop({ type: Array, required: true })
  roleList!: any[];
  @Prop({ type: Object, required: true })
  sensorIdAttrMap!: any;
  @Prop({ type: Object, required: true })
  data!: any;
  @Prop({ type: String, required: true })
  deviceId!: string;

  static focusInstance: null | StatusConditionConfig = null; // 

  // 
  keyboardFocus: boolean = false;
  widgetFocus: boolean = false;
  cursorIndex: number | null = null;
  // 
  symbolList: any[] = [
    { type: "symbol", code: "add", value: "+" },
    { type: "symbol", code: "subtract", value: "-" },
    { type: "symbol", code: "multiply", value: "", realValue: "*" },
    { type: "symbol", code: "divide", value: "", realValue: "/" },
    { type: "symbol", code: "rt", value: ">" },
    { type: "symbol", code: "rtEqual", value: "", realValue: ">=" },
    { type: "symbol", code: "lt", value: "<" },
    { type: "symbol", code: "ltEqual", value: "", realValue: "<=" },
    { type: "lp", code: "lp", value: "(" },
    { type: "rp", code: "rp", value: ")" },
    { type: "symbol", code: "and", value: "and", offline: true },
    { type: "symbol", code: "or", value: "or", offline: true },
    { type: "symbol", code: "equal", value: "==" },
    { type: "symbol", code: "notEqual", value: "!=" }
  ];
  selectConditions: any[] = [];
  status: string = "start";
  lpNum: number = 0;
  rpNum: number = 0;
  //
  formRules: any = {
    remark: { required: true, message: "" },
    statusDefinitionId: { required: true, message: "" }
  };
  // 
  personDialog: boolean = false;
  personList: any[] = [];
  personName: string = "";
  pageSize: number = 5;
  pageNum: number = 1;
  total: number = 0;
  currentSelectPerson: any = {};
  currentSelectLen: number = 0;
  selectedPerson: any = {};

  loading: boolean = false;

  howLong: string = "";
  timeUnit: any = "";
  offlineTimeUnit: Array<any> = [
    {
      value: "",
      label: ""
    },
    {
      value: "",
      label: ""
    },
    {
      value: "",
      label: ""
    }
  ];
  buttonDisabled: boolean = false;
  buttonDisabled2: boolean = false;

  // 
  get showExpression() {
    // console.log(this.selectConditions);
    return this.selectConditions
      .map((item: any) => {
        if (item.type === "sensor") {
          if (!item.sensorId) {
            return `  `;
          }
          let sensor = this.sensorList.find((d: any) => d.id === item.sensorId);
          if (!sensor) {
            return ``;
          }
          let sensorAttrList = this.sensorIdAttrMap[item.sensorId];
          if (!sensorAttrList) {
            return ` ${sensor.name} `;
          }
          let sensorAttr = sensorAttrList.find((d: any) => d.code === item.sensorAttrCode);
          return ` ${sensor.name}${sensorAttr ? sensorAttr.name : ""} `;
        } else if (item.type === "offline") {
          if (!item.sensorId) {
            return ``;
          }
          let sensor = this.sensorList.find((d: any) => d.id === item.sensorId);
          if (!sensor) {
            return ``;
          }
          // let sensorAttrList = this.sensorIdAttrMap[item.sensorId];
          // if (!sensorAttrList) {
          //   return ` ${sensor.name}  ${item.value} ${item.unit} `;
          // }
          // let sensorAttr = sensorAttrList.find((d: any) => d.code === item.sensorAttrCode);
          // return ` ${sensor.name}${sensorAttr ? sensorAttr.name : ""} `;
          return ` ${sensor.name}  ${item.value} ${item.unit} `;
        }
        return item.value;
      })
      .join(" ");
  }

  // 
  get realExpression() {
    return this.selectConditions
      .map((item: any) => {
        if (item.type === "sensor") {
          return `${item.sensorId}_${item.sensorAttrCode}`;
        } else if (item.type === "offline") {
          let multiple: number = 1;
          switch (item.unit) {
            case "":
              multiple = 60 * 60;
              break;
            case "":
              multiple = 60;
              break;
            case "":
              multiple = 1;
              break;
          }
          return item.sensorId && `('${item.sensorId}', ${item.value * multiple})`;
        }
        return item.realValue || item.value;
      })
      .join(" ");
  }

  //  
  get accessStatusList() {
    return this.allStatusList.filter((d: any) => !this.disableStatusIdMap[d.id] || d.id === this.data.statusDefinitionId);
  }

  created() {
    this.initData();
    document.addEventListener("click", this.handleBlurRuleInput);
    document.addEventListener("keydown", this.handleKeydown);
  }

  destroyed() {
    document.removeEventListener("click", this.handleBlurRuleInput);
    document.removeEventListener("keydown", this.handleKeydown);
    if (StatusConditionConfig.focusInstance && StatusConditionConfig.focusInstance === this) {
      StatusConditionConfig.focusInstance = null;
    }
  }

  initData() {
    let selectConditions = this.data.expressionJson;
    selectConditions = selectConditions ? JSON.parse(selectConditions) : [];
    this.selectConditions = selectConditions;
    selectConditions.forEach((item: any) => {
      if (item.type === "sensor" && !this.sensorIdAttrMap[item.sensorId]) {
        this.sensorIdAttrMap[item.sensorId] = [];
        this.$emit("getSensorAttr", item.sensorId);
      }
    });
    let map: any = {};
    let personIds = this.data.personIds || [];
    personIds.forEach((item: any) => {
      map[item] = true;
    });
    this.selectedPerson = map;
    // 
    let len = selectConditions.length;
    if (len) {
      this.status = selectConditions[len - 1].type;
      this.lpNum = selectConditions.filter((d: any) => d.type === "lp").length;
      this.rpNum = selectConditions.filter((d: any) => d.type === "rp").length;
    }
  }

  // 
  handleFocusRuleInput(index: number | null) {
    this.keyboardFocus = true;
    this.cursorIndex = index;
    if (StatusConditionConfig.focusInstance && StatusConditionConfig.focusInstance !== this) {
      StatusConditionConfig.focusInstance.handleBlurRuleInput();
    }
    StatusConditionConfig.focusInstance = this;
  }
  handleBlurRuleInput() {
    this.keyboardFocus = false;
    StatusConditionConfig.focusInstance = null;
  }
  handleFocusWidgetInput() {
    this.widgetFocus = true;
  }
  handleBlurWidgetInput() {
    this.widgetFocus = false;
  }
  get reactKeydownEvent() {
    return this.keyboardFocus && !this.widgetFocus;
  }
  handleKeydown(e: KeyboardEvent) {
    if (!this.reactKeydownEvent) return;
    switch (e.code.toLocaleLowerCase()) {
      case "backspace":
        this.handleDeleteToken();
        break;
      case "arrowleft":
        this.handleMoveCursor(false);
        break;
      case "arrowright":
        this.handleMoveCursor(true);
        break;
    }
  }
  // 
  // start: 
  // symbol: 
  // lp: 
  // rp: 
  // input: 
  // sensor: 
  handleSelectKey(item: any) {
    // switch (this.status) {
    //   case "start":
    //     if (item.type === "symbol") return;
    //     break;
    //   case "symbol":
    //     if (item.type === "symbol") return;
    //     break;
    //   case "lp":
    //     if (item.type === "symbol") return;
    //     break;
    //   case "rp":
    //     if (item.type !== "symbol" && item.type !== "rp") return;
    //     break;
    //   case "input":
    //     if (item.type !== "symbol" && item.type !== "rp") return;
    //     break;
    //   case "sensor":
    //     if (item.type !== "symbol" && item.type !== "rp") return;
    //     break;
    // }
    // switch (item.code) {
    //   case "lp":
    //     this.lpNum++;
    //     break;
    //   case "rp":
    //     if (this.rpNum === this.lpNum) return;
    //     this.rpNum++;
    //     break;
    // }
    this.status = item.type;
    let data = { ...item };
    if (this.cursorIndex === null) {
      this.selectConditions.push(data);
    } else {
      this.selectConditions.splice(this.cursorIndex, 0, data);
      this.cursorIndex++;
    }
    if (item.type === "offline") {
      this.buttonDisabled = true;
      this.buttonDisabled2 = false;
    } else if (item.type === "sensor" || item.type === "input") {
      this.buttonDisabled = false;
      this.buttonDisabled2 = true;
    }
  }

  // 
  handleDeleteToken() {
    let item: any;
    if (this.cursorIndex === null) {
      item = this.selectConditions.pop();
    } else if (this.cursorIndex > 0) {
      item = this.selectConditions.splice(this.cursorIndex - 1, 1);
    }
    if (!item) return;
    this.cursorIndex !== null && this.cursorIndex--;
    switch (item.code) {
      case "lp":
        this.lpNum--;
        break;
      case "rp":
        this.rpNum--;
        break;
    }
    let last = this.selectConditions[this.selectConditions.length - 1];
    this.status = last ? last.type : "start";
    if (this.status === "start") {
      this.buttonDisabled = false;
      this.buttonDisabled2 = false;
    }
  }

  // 
  handleMoveCursor(arrow: boolean) {
    if (arrow) {
      if (this.cursorIndex !== null && this.cursorIndex < this.selectConditions.length - 1) {
        this.cursorIndex++;
      } else if (this.cursorIndex === this.selectConditions.length - 1) {
        this.cursorIndex = null;
      }
    } else {
      if (this.cursorIndex === null) {
        this.cursorIndex = this.selectConditions.length - 1;
      } else if (this.cursorIndex > 0) {
        this.cursorIndex--;
      }
    }
  }

  // 
  async checkExpression() {
    // console.log("");
    // console.log(this.realExpression);
    let {
      data: { status, message }
    } = await this.$http.post(api.checkExpression, {
      expression: this.realExpression
    });
    if (status) {
      this.$message.success("");
    } else {
      this.$message.error(message);
    }
  }

  // 
  handleSelectSensor(sensorId: string, item: any) {
    this.$set(item, "sensorAttrCode", "");
    this.$set(item, "disabled", false);
    if (!this.sensorIdAttrMap[sensorId]) {
      this.$emit("getSensorAttr", sensorId);
    }
  }

  // 
  handleSelectPerson() {
    this.personDialog = true;
    this.currentSelectPerson = { ...this.selectedPerson };
    this.getPersonList();
  }

  handleCurrentChange(val: number) {
    this.pageNum = val;
    this.getPersonList();
  }

  async getPersonList() {
    let {
      data: { data, status, message }
    } = await this.$http.post(api.getPersonPaging, {
      query: {
        name: this.personName,
        deviceId: this.deviceId
      },
      current: this.pageNum,
      size: this.pageSize
    });
    if (status) {
      this.personList = data.records;
      this.total = data.total;
      this.$nextTick(() => {
        this.personList.forEach((row: any) => {
          (this.$refs.table as ElTable).toggleRowSelection(row, !!this.currentSelectPerson[row.id]);
        });
        this.currentSelectLen = this.personList.filter((row: any) => !!this.currentSelectPerson[row.id]).length;
      });
    } else {
      this.$message.error(message);
    }
  }

  // 
  handleSelectTable(selection: any[], row: any) {
    if (this.currentSelectLen < selection.length) {
      this.currentSelectPerson[row.id] = row;
    } else {
      delete this.currentSelectPerson[row.id];
    }
    this.currentSelectLen = selection.length;
  }

  handleSelectAllTable(selection: any[]) {
    if (selection.length) {
      selection.forEach((row: any) => {
        this.currentSelectPerson[row.id] = row;
      });
    } else {
      this.personList.forEach((row: any) => {
        delete this.currentSelectPerson[row.id];
      });
    }
    this.currentSelectLen = selection.length;
  }

  handleSurePerson() {
    this.selectedPerson = JSON.parse(JSON.stringify(this.currentSelectPerson));
    this.personDialog = false;
  }

  // 
  handleDeleteCondition() {
    if (this.data.id) {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.deleteCondition();
      });
    } else {
      this.$emit("delete");
    }
  }

  // 
  handleSaveCondition() {
    (this.$refs.form as ElForm).validate(async flag => {
      if (flag) {
        if (
          this.selectConditions.some((item: any) => {
            return item.type === "sensor" ? !item.sensorId || !item.sensorAttrCode : false;
          })
        ) {
          this.$message.info("");
        } else {
          this.doSaveCondition();
        }
      }
    });
  }

  async doSaveCondition() {
    this.loading = true;
    try {
      let {
        data: { status, message }
      } = await this.$http.post(this.data.id ? api.updateStatusRule : api.addStatusRule, {
        deviceId: this.deviceId,
        expression: this.realExpression,
        expressionShow: this.showExpression,
        expressionJson: JSON.stringify(this.selectConditions),
        id: this.data.id,
        personIds: Object.keys(this.selectedPerson),
        remark: this.data.remark,
        roleIds: this.data.roleIds,
        sensorIds: this.selectConditions.filter((d: any) => d.type === "sensor").map((d: any) => d.sensorId),
        statusDefinitionId: this.data.statusDefinitionId,
        pushMsg: this.data.pushMsg
      });
      if (status) {
        this.$message.success("");
        this.$emit("update");
      } else {
        this.$message.error(message);
      }
    } finally {
      this.loading = false;
    }
  }

  // 
  async deleteCondition() {
    let {
      data: { status, message }
    } = await this.$http.get(api.deleteByWarningTermId + this.data.id);
    if (status) {
      this.$message.success("");
      this.$emit("update");
    } else {
      this.$message.error(message);
    }
  }
}
