




































































import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import api from "@/microApps/deviceManage/api";
import SensorDetail from "@/microApps/sensorManage/views/sensorManage/components/SensorDetail.vue";

@Component({
  components: { SensorDetail }
})
export default class SensorList extends Vue {
  @Prop({ type: String })
  deviceId!: string;

  sensorList: any = [];

  noBindSensorList: any = [];

  noBindSensorDialogVisible: boolean = false;

  loading: boolean = false;
  dialogLoading: boolean = false;

  multipleSelection: any = [];

  //
  pageParam2: any = {
    current: 1,
    size: 10,
    query: {
      name: "",
      bound: "false"
    }
  };

  //visible
  sensorDialogVisible: boolean = false;
  dataForm: object = {};
  sensorDialogTitle: string = "";

  handleSelectionChange(val: Array<any>) {
    this.multipleSelection = val;
  }
  mounted() {
    this.getSensorList();
  }

  // 
  handleAddSensor() {
    this.dataForm = {};
    this.sensorDialogVisible = true;
    this.sensorDialogTitle = "";
  }

  // 
  handleEdit(data: any) {
    this.sensorDialogTitle = "";
    this.dataForm = JSON.parse(JSON.stringify(data));
    this.sensorDialogVisible = true;
  }

  closeDialog() {
    this.sensorDialogVisible = false;
  }

  //
  getSensorList() {
    this.loading = true;
    this.$http
      .get(api.getBindSensor + this.deviceId)
      .then((res: any) => {
        if (res.data.status) {
          this.sensorList = res.data.data;
          this.$emit("update", this.sensorList);
        } else {
          this.$message.error(res.data.message);
        }
      })
      .finally(() => {
        this.loading = false;
      });
  }

  //
  getNoBindSensorList() {
    this.dialogLoading = true;
    this.$http
      .post(api.getSensorPage, this.pageParam2)
      .then((res: any) => {
        if (res.data.status) {
          this.noBindSensorList = res.data.data.records;
          this.pageParam2.total = res.data.data.total;
        } else {
          this.$message.error(res.data.message);
        }
      })
      .finally(() => {
        this.dialogLoading = false;
      });
  }

  //
  handleSizeChange2(val: Number) {
    this.pageParam2.size = val;
    this.getNoBindSensorList();
  }

  handleCurrentChange2(val: Number) {
    this.pageParam2.current = val;
    this.getNoBindSensorList();
  }

  saveBind() {
    if (this.multipleSelection.length == 0) {
      this.$message.warning("!");
      return;
    }
    this.dialogLoading = true;
    let url = api.deviceBindSensor.concat(this.deviceId);
    this.$http
      .post(url, this.multipleSelection.map((value: any) => value.id))
      .then((res: any) => {
        if (res.data.status) {
          this.$message.success("!");
          this.getSensorList();
          this.noBindSensorDialogVisible = false;
        } else {
          this.$message.error(res.data.message);
        }
      })
      .finally(() => {
        this.dialogLoading = false;
      });
  }

  //TODO 
  bindSensor() {
    this.noBindSensorDialogVisible = true;
    this.pageParam2 = {
      current: 1,
      size: 10,
      query: {
        name: "",
        bound: "false"
      }
    };
    this.getNoBindSensorList();
  }

  //TODO 
  cancelBind(data: any) {
    this.loading = true;
    this.$http
      .post(api.deleteBindSensor, {
        deviceId: this.deviceId,
        sensorId: data
      })
      .then((res: any) => {
        if (res.data.status) {
          this.$message.success("");
          this.getSensorList();
        } else {
          this.$message.error(res.data.message);
        }
      })
      .finally(() => {
        this.loading = false;
      });
  }
}
