




































































































































































































import { Component, Emit, Prop, Vue, Watch } from "vue-property-decorator";
import Utils from "@/utils/utils";
import DeviceCondition from "./components/DeviceCondition.vue";
import DeviceStyle from "./components/DeviceStyle.vue";
import DeviceSensor from "./components/DeviceSensor.vue";
import StatusAlarmCondition from "./components/StatusAlarmCondition.vue";
import EventAlarmCondition from "./components/EventAlarmCondition.vue";

const AMap: any = (window as any).AMap;
const AMapUI: any = (window as any).AMapUI;
import api from "@/microApps/deviceManage/api";
import { ElForm } from "element-ui/types/form";
import { ElUpload, ElUploadInternalFileDetail } from "element-ui/types/upload";
import { validateImage } from "@/utils/validate";

@Component({
  components: { DeviceCondition, DeviceSensor, DeviceStyle, StatusAlarmCondition, EventAlarmCondition }
})
export default class DeviceDetails extends Vue {
  loading: any = false;

  @Prop({ type: String })
  deviceId!: string;

  activeName: string = "1";
  // 
  customOptions: Array<any> = [];
  // ()
  deviceDetailsForm: any = {};
  // 
  orgOptions: Array<any> = [];
  orgVisible: boolean = false;
  // ()
  deviceDetailsFormRules: any = {
    // code: [
    //   {
    //     required: true,
    //     message: "",
    //     trigger: "change"
    //   }
    // ],
    name: [
      {
        required: true,
        message: "",
        trigger: "change"
      }
    ],
    typeDetailId: [
      {
        required: true,
        message: "",
        trigger: "change"
      }
    ],
    orgId: [
      {
        required: true,
        message: "",
        trigger: "change"
      }
    ]
  };
  // 
  deviceTypeOptions: Array<any> = [];
  typeVisible: boolean = false;
  hasCustomRole: boolean = qTouch.getBaseTypeByCode("device");
  // 
  statusOptions: Array<any> = [
    {
      value: true,
      label: ""
    },
    {
      value: false,
      label: ""
    }
  ];
  // 
  disabled: boolean = false;
  // 
  visible: boolean = true;
  // 
  pagination: any = {
    // 
    condition: "",
    // 
    tableData: [],
    // 
    currentPage: 1,
    // 
    pageSize: 10,
    // 
    total: 0
  };
  classifyOptions: any = [];
  // prop
  typeDetailProps: any = {
    label: "name",
    value: "id",
    checkStrictly: true
  };

  // 
  classVisible: boolean = false;

  // 
  map: any;
  marker: any = null;
  addressDialog = false;
  text = "";
  showLeaveChoose = false;
  zoom = 18;
  center = [106.569137124949, 29.643814122592];

  // 
  allStatusList: any[] = [];
  sensorList: any[] = [];

  //
  @Emit("switch")
  switchToDeviceList() {}

  changeListRowId(data: string) {
    this.$emit("update:deviceId", data);
  }

  @Watch("deviceId", { immediate: true })
  deviceIdChange() {
    this.deviceId && this.getDeviceInfo(this.deviceId);
    this.getClassifyTree();
    this.getOrganizations();
    this.getDeviceTypes();
  }

  // 
  async getDeviceTypeStatusList() {
    let {
      data: { data, status, message }
    } = await this.$http.get(api.getDeviceTypeStatusList + this.deviceDetailsForm.typeDetailId);
    if (status) {
      this.allStatusList = data.filter((d: any) => !d.defaultStatus);
    } else {
      this.$message.error(message);
    }
  }

  // 
  getClassifyTree() {
    this.classVisible = true;
    this.$http.get(api.classifyTree).then((res: any) => {
      if (res.data.status) {
        this.classifyOptions = res.data.data;
        // this.$nextTick(() => {
        //   let deviceClassifyIds = this.deviceDetailsForm.deviceClassifyIds;
        //   deviceClassifyIds && (this.$refs.classTree as ElTree).setCheckedKeys(deviceClassifyIds);
        // });
      } else {
        this.$message.error(res.data.message);
      }
    });
  }

  // 
  getOrganizations() {
    this.orgVisible = true;
    this.$http.get(api.getNewOrgTree).then((response: any) => {
      if (response.data.status) {
        this.orgOptions = response.data.data;
        // this.$nextTick(() => {
        //   (this.$refs.orgTree as ElTree).setCheckedKeys([this.vehicleDetailsForm.orgId]);
        // });
      } else {
        this.$message.error(response.data.message);
      }
    });
  }

  // 
  saveOrg() {
    // let typeDetailIds = (this.$refs.orgTree as ElTree).getCheckedKeys();
    // if (typeDetailIds.length) {
    //   this.vehicleDetailsForm.orgId = typeDetailIds[0];
    // } else {
    //   this.vehicleDetailsForm.orgId = "";
    // }
    // this.orgVisible = false;
  }

  // 
  saveClass() {
    // this.deviceDetailsForm.deviceClassifyIds = (this.$refs.classTree as ElTree).getCheckedKeys();
    // this.classVisible = false;
  }

  // 
  getDeviceInfo(id: any) {
    this.loading = true;
    this.$http.get(api.getOneById + "/" + id).then((response: any) => {
      this.loading = false;
      if (response.data.status) {
        this.deviceDetailsForm = response.data.data;
        if (this.deviceDetailsForm.extInfoVO) {
          this.customOptions = this.deviceDetailsForm.extInfoVO;
        }
        this.getDeviceTypeStatusList();
      } else {
        this.$message.error(response.data.message);
      }
    });
  }

  handleEditClass() {
    this.getClassifyTree();
  }

  // 
  getDeviceTypes() {
    // this.typeVisible = true;
    this.$http.get(api.findTypeTree + "/device").then((response: any) => {
      if (response.data.status) {
        this.deviceTypeOptions = response.data.data;
        // this.$nextTick(() => {
        //   let typeDetailId = this.deviceDetailsForm.typeDetailId;
        //   typeDetailId && (this.$refs.typeTree as ElTree).setCheckedKeys([typeDetailId]);
        // });
        // Utils.removeEmptyChildren(this.deviceTypeOptions);
      } else {
        this.$message.error(response.data.message);
      }
    });
  }

  // 
  handleTypeChange(data: any, checked: boolean, node: boolean) {
    if (checked) {
      (this.$refs.typeTree as any).setCheckedKeys([data.id]);
    }
  }

  // 
  saveType() {
    // let typeDetailIds = (this.$refs.typeTree as ElTree).getCheckedKeys();
    // this.$set(this.deviceDetailsForm, "typeDetailId", typeDetailIds.length ? typeDetailIds[0] : "");
    (this.$refs.deviceDetailsForm as ElForm).validateField("typeDetailId", () => {});
    // this.typeVisible = false;
    if (this.deviceDetailsForm.typeDetailId) {
      this.getCustomOptions();
    }
  }

  // 
  getCustomOptions() {
    this.$http.get(api.getTemplateByTypeDetaiId + "/" + this.deviceDetailsForm.typeDetailId).then((response: any) => {
      if (response.data.status) {
        this.customOptions = response.data.data;
      } else {
        this.$message.error(response.data.message);
      }
    });
  }

  // CustomInput
  async getFilledCustomOptions() {
    let customInput: any = this.$refs["custom-input"];
    if (!customInput) {
      return null;
    }
    return await customInput.formValidation();
  }

  saveDevice() {
    (this.$refs.deviceDetailsForm as any).validate((valid: any) => {
      if (valid) {
        this.loading = true;
        if (this.deviceDetailsForm.needUpload) {
          delete this.deviceDetailsForm.needUpload;
          (this.$refs.upload as ElUpload).submit();
        } else {
          this.doSubmit();
        }
      }
    });
  }

  // 
  async doSubmit() {
    try {
      let data = await this.getFilledCustomOptions();
      if (data) {
        this.deviceDetailsForm.extInfo = await this.getFilledCustomOptions();
      }
      delete this.deviceDetailsForm.extInfoVO;
      let url = api.add;
      if (this.deviceId) {
        url = api.update;
      }
      this.$http
        .post(url, this.deviceDetailsForm)
        .then((response: any) => {
          if (response.data.status) {
            this.$message.success(response.data.message + "");
            response.data.data && this.changeListRowId(response.data.data);
            this.switchToDeviceList();
          } else {
            this.$message.error(response.data.message);
          }
        })
        .finally(() => {
          this.loading = false;
        });
    } finally {
      this.loading = false;
    }
  }

  // 
  customHttpRequest(data: any) {
    let fData = Utils.createUploadForm(data.file);
    this.$http
      .post(data.action, fData.formData, fData.config)
      .then((response: any) => {
        if (response.data.status) {
          this.deviceDetailsForm.image = response.data.data.accessUrl;
          this.doSubmit();
        } else {
          this.$message.error("");
        }
      })
      .finally(() => {
        this.loading = false;
      });
  }

  customChange(file: ElUploadInternalFileDetail, fileList: ElUploadInternalFileDetail[]) {
    let { status, message } = validateImage(file.raw);
    if (status) {
      this.$set(this.deviceDetailsForm, "image", file.url);
      this.deviceDetailsForm.needUpload = true;
    } else {
      this.$message.error(message);
      (this.$refs.upload as any).handleRemove(file);
    }
  }

  handleRemove() {
    this.deviceDetailsForm.image = "";
    this.$nextTick(() => {
      (this.$refs.upload as ElUpload).clearFiles();
    });
  }

  // 
  bindAddress() {
    this.addressDialog = true;
    this.$nextTick(() => {
      // 
      if (this.deviceDetailsForm.location) {
        let lnglat = JSON.parse(this.deviceDetailsForm.location);
        this.initMap(lnglat); //  
        this.addBuildMarker(lnglat, this.deviceDetailsForm.address); // 
        this.showLeaveChoose = false;
        this.text = "" + this.deviceDetailsForm.location;
      }
      // 
      else {
        this.text = "";
        // 
        this.initMap(this.center);
        this.chooseSiteOnMap();
      }
    });
  }

  // 
  initMap(center: any) {
    const _this = this;
    this.map = new AMap.Map("deviceAmap", {
      center: center,
      zoom: this.zoom,
      features: ["bg", "point", "road", "building"]
    });
    AMapUI.loadUI(["misc/PoiPicker"], function(PoiPicker: any) {
      var poiPicker = new PoiPicker({
        input: "deviceInput"
      });
      //poiPicker
      _this.poiPickerReady(poiPicker);
    });
  }

  poiPickerReady(poiPicker: any) {
    const _this = this;
    (window as any).poiPicker = poiPicker;
    let marker = new AMap.Marker();
    // let infoWindow = new AMap.InfoWindow({
    //   offset: new AMap.Pixel(0, -20)
    // });
    // POI
    poiPicker.on("poiPicked", function(poiResult: any) {
      let source = poiResult.source;
      let poi = poiResult.item;
      let info = {
        source: source,
        id: poi.id,
        name: poi.name,
        location: poi.location.toString(),
        address: poi.formattedAddress
      };
      marker.setMap(_this.map);
      // infoWindow.setMap(_this.map);
      marker.setPosition(poi.location);
      // infoWindow.setPosition(poi.location);
      // // infoWindow.setContent("POI: <pre>" + JSON.stringify(info, null, 2) + "</pre>");
      // infoWindow.open(_this.map, marker.getPosition());
      _this.map.setCenter(marker.getPosition());
    });
  }

  // marker
  addBuildMarker(position: any, address: string) {
    let _this = this;
    this.marker = new AMap.Marker({
      position: position
    });
    this.marker.setLabel({
      //labelmaker
      offset: new AMap.Pixel(20, -20),
      content: "<div style='margin: 2px 5px;font-family: PingFang SC;line-height: 16px'>" + address + "</div>"
    });
    this.marker.setMap(this.map);
  }

  // 
  chooseOff() {
    this.text = "";
    this.showLeaveChoose = false;
    this.initMap(this.center);
  }

  // 
  chooseSiteOnMap() {
    let _this = this;
    this.showLeaveChoose = true;
    this.text = "";
    this.map.clearMap();
    // (  )
    this.map.setDefaultCursor("crosshair");
    this.map.on("dblclick", function(e: any) {
      let lnglat = [e.lnglat.getLng(), e.lnglat.getLat()];
      _this.text = " " + JSON.stringify(lnglat) + " ";
      let geocoder = new AMap.Geocoder({
        radius: 1000,
        extensions: "all"
      });
      geocoder.getAddress(lnglat, function(status: any, result: any) {
        if (status === "complete" && result.info === "OK") {
          let address = result.regeocode.formattedAddress; //
          _this
            .$prompt("", "", {
              confirmButtonText: "",
              cancelButtonText: "",
              inputValue: address
            })
            .then((obj: any) => {
              _this.deviceDetailsForm.location = JSON.stringify(lnglat);
              _this.deviceDetailsForm.address = obj.value;
              _this.addressDialog = false;
            })
            .catch(() => {
              // _this.$message.info("");
              _this.text = "";
            });
        }
      });
    });
  }

  destroyMap() {
    this.map.destroy();
  }
}
