import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import api from "@/microApps/deviceManage/api";
import EventConditionConfig from "@/microApps/deviceManage/views/deviceManage/components/EventConditionConfig.vue";
let uuid = 1;

@Component({
  components: { EventConditionConfig }
})
export default class DeviceAlarmCondition extends Vue {
  @Prop({ type: String, required: true })
  deviceId!: string;
  @Prop({ type: Array, required: true })
  allStatusList!: any[];
  conditions: any[] = [];
  sensorList: any[] = [];
  allEventList: any[] = [];
  disableEventIdMap: any = {};
  roleList: any[] = [];
  sensorIdAttrMap: any = {};

  created() {
    this.getSensorList();
    this.getConditionList();
    this.getEventList();
    this.getRoleList();
  }

  handleAddCondition() {
    this.conditions.unshift({
      key: uuid++
    });
  }

  async getConditionList() {
    let {
      data: { data, status, message }
    } = await this.$http.get(api.listEventTerms + this.deviceId);
    if (status) {
      this.conditions = data;
      let map: any = {};
      data.forEach((d: any) => {
        map[d.eventDefinitionId] = true;
      });
      this.disableEventIdMap = map;
    } else {
      this.$message.error(message);
    }
  }

  // 
  async getSensorList() {
    let {
      data: { data, status, message }
    } = await this.$http.get(api.getBindSensor + this.deviceId);
    if (status) {
      this.sensorList = data;
    } else {
      this.$message.error(message);
    }
  }

  // 
  async getSensorAttr(sensorId: string) {
    let {
      data: { data, status, message }
    } = await this.$http.get(api.getSensorAttr + sensorId);
    if (status) {
      let map = { ...this.sensorIdAttrMap };
      map[sensorId] = data;
      this.sensorIdAttrMap = map;
    } else {
      this.$message.error(message);
    }
  }

  // 
  async getEventList() {
    let {
      data: { data, status, message }
    } = await this.$http.post(api.getEventDefinition, {
      current: 0,
      size: 9999
    });
    if (status) {
      this.allEventList = data.records;
    } else {
      this.$message.error(message);
    }
  }

  // 
  async getRoleList() {
    let {
      data: { data, status, message }
    } = await this.$http.get(api.getRoleList);
    if (status) {
      this.roleList = data;
    } else {
      this.$message.error(message);
    }
  }
}
